<template>
  <div class="container">
    <div class="nav-head-title">
      <span class="p-span">
        <span>首页 </span> &nbsp;/&nbsp; 
        <span>订单 </span> &nbsp;/&nbsp; 
        <span class="p-span-active">订单详情</span>
      </span>
    </div>
    <el-row>
      <el-col :span="24"><div class="grid-content bg-purple-dark" id="order_id">订单号：{{ order_id }}</div></el-col>
    </el-row>
    <el-row :gutter="20">
      <el-col :span="7"><div class="grid-content bg-purple">下单时间：{{ create_time }}</div></el-col>
      <el-col :span="5"><div class="grid-content bg-purple">金额：{{ price }}</div></el-col>
      <el-col :span="6"><div class="grid-content bg-purple">支付方式：COD</div></el-col>
      <el-col :span="6"><div class="grid-content bg-purple">订单状态：{{ status }}</div></el-col>
    </el-row>
    <el-row>
      <el-button type="danger" @click="open" round>取消</el-button>
      <el-button type="primary" @click="open" round>生成新订单</el-button>
      <el-button type="success" @click="open" round>签收</el-button>
      <el-button type="info" @click="open" round>拒签</el-button>
      <el-button type="danger" @click="dialogFormVisible = true" round>设为待处理订单</el-button>
      <el-button type="success" @click="open" round>确认</el-button>
    </el-row>
  <h2>联系人信息</h2>
  <table class="tb" style="width:100%;">
    <tr>
      <th>姓名</th>
      <td>{{ name }}</td>
      <th>电话</th>
      <td>{{ phone }}</td>
    </tr>
    <tr>
      <th>国家/地区</th>
      <td>{{ country }}</td>
      <th>省/市</th>
      <td>{{ province }}</td>
    </tr>
    <tr>
      <th>县</th>
      <td>{{ city }}</td>
      <th>区</th>
      <td>{{ area }}</td>
    </tr>
    <tr>
      <th>地址</th>
      <td>{{ address }}</td>
      <th>邮政编码</th>
      <td>{{ post_code }}</td>
    </tr>
    <tr>
      <th>备注</th>
      <td>{{ remark }}</td>
      <th>ip</th>
      <td>{{ ip }}</td>
    </tr>
  </table>

  <h2>订单信息</h2>
  <table class="tb" style="width:100%;">
    <tr>
      <th>sku</th>
      <th>标题</th>
      <th>数量</th>
      <th>单价</th>
    </tr>
    <tr v-for="orderGood in orderGoods">
      <td>{{ orderGood.sku_code }}</td>
      <td>{{ orderGood.pname }}</td>
      <td>{{ orderGood.number }}</td>
      <td>{{ orderGood.price }}</td>
    </tr>
  </table>

  <h2>发货信息</h2>
  <table class="tb" style="width:100%;">
    <tr>
      <th>货代</th>
      <th>落地配公司</th>
      <th>物流单号</th>
      <th>发货时间</th>
      <th>物流状态</th>
    </tr>
    <tr v-for="trackLog in trackLogs">
      <td>{{ trackLog.freight_forwarder }}</td>
      <td>{{ trackLog.landing_company }}</td>
      <td>{{ trackLog.number }}</td>
      <td>{{ trackLog.track_time }}</td>
      <td>{{ trackLog.track_status }}</td>
    </tr>
  </table>

  <el-card class="box-card">
    <div slot="header" class="clearfix">
      <h2>操作日志</h2>
    </div>
    <div v-for="orderLog in orderLogs" class="text item">
      操作人：{{ orderLog.action_user }}&nbsp;&nbsp;&nbsp;&nbsp;时间：{{ orderLog.log_time }}<br/>
      修改订单状态：{{ orderLog.old_status + ' =》' + orderLog.new_status }}<br/>
      备注：{{ orderLog.remark }}<br/><br/>
    </div>
  </el-card>

  <el-dialog title="标注问题单" :visible.sync="dialogFormVisible">
  <el-form :model="form" :rules="rules" ref="form">
    <el-form-item label="物流单号" :label-width="formLabelWidth" prop="track_number">
      <el-input v-model="form.track_number" type="text" auto-complete="off"></el-input>
    </el-form-item>
    <el-form-item label="问题类型" :label-width="formLabelWidth" prop="type">
      <el-select v-model="form.type" placeholder="请选择">
        <el-option label="其他" value="0"></el-option>
        <el-option label="地址有误" value="1"></el-option>
        <el-option label="电话有误" value="2"></el-option>
        <el-option label="电话无人接听" value="3"></el-option>
        <el-option label="签收失败" value="4"></el-option>
        <el-option label="无人收货" value="5"></el-option>
        <el-option label="客户无法现金支付" value="6"></el-option>
        <el-option label="客户要求取消订单" value="7"></el-option>
        <el-option label="客户希望换一天送货" value="8"></el-option>
        <el-option label="客户拒绝付款" value="9"></el-option>
        <el-option label="电话地址都有误" value="10"></el-option>
        <el-option label="客户说未曾订购" value="11"></el-option>
        <el-option label="颜色不详" value="12"></el-option>
        <el-option label="尺寸不详" value="13"></el-option>
        <el-option label="件数不详" value="14"></el-option>
      </el-select>
    </el-form-item>
    <el-form-item label="问题状态" :label-width="formLabelWidth" prop="status">
      <el-select v-model="form.status" placeholder="请选择">
        <el-option label="处理中" value="0"></el-option>
        <el-option label="重新派送" value="1"></el-option>
        <el-option label="取消订单" value="2"></el-option>
        <el-option label="拒签" value="3"></el-option>
        <el-option label="签收" value="4"></el-option>
      </el-select>
    </el-form-item>
    <el-form-item label="备注" :label-width="formLabelWidth" prop="remark">
      <el-input v-model="form.remark" type="textarea" auto-complete="off"></el-input>
    </el-form-item>
  </el-form>
  <div slot="footer" class="dialog-footer">
    <el-button @click="dialogFormVisible = false">取 消</el-button>
    <el-button type="primary" @click="pSubmit('form')">提 交</el-button>
  </div>
</el-dialog>
  </div>
</template>

<script>
import axios from 'axios'

export default {
  data(){
    return {
      order_id:"",
      create_time:"",
      price:"",
      status:"",
      name:"",
      phone:"",
      country:"",
      province:"",
      city:"",
      area:"",
      address:"",
      post_code:"",
      remark:"",
      ip:"",
      orderGoods:[],
      orderLogs:[],
      trackLogs:[],
      dialogFormVisible: false,
      form: {
        remark: '',
        type: [],
        track_number: '',
        status: [],
      },
      rules: {
        track_number: [
          { required: true, message: '请输入物流单号', trigger: 'blur' }
        ],
        type: [
          { required: true, message: '请选择问题类型', trigger: 'change' }
        ],
        status: [
          { required: true, required: true, message: '请选择问题状态', trigger: 'change' }
        ],
        remark: [
          { required: true, required: true, message: '请输入备注', trigger: 'blur' }
        ],

      },
      formLabelWidth: '120px',
    }
  },
  methods: {
      open(clk) {
        this.$confirm('是否继续此操作?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          var s = location.search;
          if(s.indexOf('?')==-1)return false;
          var ss = s.split("id=")[1].split("&")[0];
          var that2 = this;
          var status = '';
          if(clk.path[0].innerText=='取消'){
            status = 10;
          }else if(clk.path[0].innerText=='签收'){
            status = 9;
          }else if(clk.path[0].innerText=='拒签'){
            status = 13;
          }else if(clk.path[0].innerText=='确认'){
            status = 2;
          }else if(clk.path[0].innerText=='生成新订单'){ 
            window.location.href = '/order/newOrder'+s;
            return false;
          }else{
            that2.$message({
              type: 'info',
              message: '无法修改'
            });
            return false;
          }
                    
          axios({
            method:"POST",
            url:'http://192.168.10.141/v1/order/update-status',
            data:{'status':status,'order_id':ss},
          }).then(function(re){
            console.log(re);
            if(re.data.error != ''){
              that2.$message({
                type: 'info',
                message: re.data.error
              });
            }else{
              that2.$message({
                type: 'success',
                message: '成功!'
              });
            }
          });

        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消'
          });          
        });
      },
      pSubmit(formName){
        this.$refs[formName].validate((valid) => {
          if (valid) {
            let that = this;
            this.$axios.post('http://192.168.10.141/v1/order/update-status',{'status':20,'order_id':this.order_id,'type':this.form.type,'remark':this.form.remark,'track_number':this.form.track_number,'sonStatus':this.form.status}).then((re) => {
              console.log(re);
              this.dialogFormVisible = false;
              if(re.data.error != ''){
                that.$message({
                  type: 'info',
                  message: re.data.error
                });
              }else{
                that.$message({
                  type: 'success',
                  message: '成功!'
                });
              }
            }).catch((re) => {
                console.log(re);
            });
          } else {
            console.log('error submit!!');
            return false;
          }
        });

      },
  },
  created() {
    var s = location.search;
    let that = this;
    axios({
      method:"GET",
      url:'http://192.168.10.141/v1/order/details'+s,
    }).then(function(re){
      console.log(re);
      var data = re.data.data;
      that.order_id = data.order.id;
      that.create_time = data.order.create_time;
      that.price = data.order.total_price;
      that.status = data.order.status;
      that.name = data.order.user_name;
      that.phone = data.order.phone;
      that.country = data.order.country;
      that.province = data.order.province;
      that.city = data.order.city;
      that.area = data.order.area;
      that.address = data.order.address;
      that.post_code = data.order.post_code;
      that.remark = data.order.remark;
      that.ip = data.order.user_ip;
      that.orderGoods = data.orderGoods;
      that.orderLogs = data.orderLogs;
      that.trackLogs = data.trackLogs;
    });

  }
}

</script>

<style>
/* 字体hover主色 13c19f */
/* hover背景色 eef1f9 */
.container {
  padding: 30px 30px 0;
  overflow: auto;
}
.main-container{
    background-color:white;
    width:99%;   
}
.el-col {
  border-radius: 4px;
}
.bg-purple-dark {
  background: #99a9bf;
}
.bg-purple {
  background: #d3dce6;
}
.bg-purple-light {
  background: #e5e9f2;
}
.grid-content {
  border-radius: 4px;
  min-height: 36px;
}
.for-holder-color .el-input__inner::-webkit-input-placeholder {
  color: black;
}
.for-holder-color .el-input__inner:-moz-placeholder {
  color: black;
}
.for-holder-color .el-input__inner:-ms-input-placeholder {
  color: black;
}
/* 设置table trhover 字体 */
tr:hover{
    color:#13c19f;
}
.input-search{
    width:260px;float:left;margin:11px;
}
.high-search{
    float:left;margin-top:10px;font-size:14px;line-height:32px;color:#aaa;
}
.select-c{
    color:black;width:120px;float:left;margin:10px;
}
.btn-add{
    float:right;margin:5px;background-color:#13c19f;color:white;border-color:#13c19f; 
}
.page-div{
    width:100%;height:10%;
}
.select-out{
    float:right;margin:5px;width:80px;
}
.grid-content{
  line-height:35px;
}
.el-row {
  margin-bottom: 10px;
  &:last-child {
    margin-bottom: 0;
  }
}
.tb{
  border-top:1px solid black;
  border-left:1px solid black;
}
.tb th,.tb td{
  border-right:1px solid black;
  border-bottom:1px solid black;
  height:40px;
}
.tb th,.tb td{
  text-align: center;
}

</style>



